<template>
  <div>
    <el-container>
      <el-header style="text-align: left;padding-left: 0px;margin-top: 10px">
        <el-input
            placeholder="通过员工名搜索员工,记得回车哦..."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="enames">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>

         <el-button type="success" size="mini" @click="exportEmps"><i class="fa fa-lg fa-level-down" style="margin-right: 5px"></i>生成工资条
          </el-button>     
      </el-header>
      <el-main>
        <el-table
      :data="emps"
      size="mini"
      border
      stripe
      v-loading="tableLoading"
      style="width: 100%">
      <el-table-column
        type="selection"
        align="left"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        align="left"
        fixed
        label="姓名"
        width="120">
      </el-table-column>
      <el-table-column
        prop="workID"
        width="120"
        align="left"
        label="工号">
      </el-table-column>
      <el-table-column                                                                                              
      </el-table-column>                                                                                            
      <el-table-column prop="month.month_type" width="60" label="事件类型"></el-table-column>
      <el-table-column prop="month.month_salary" width="60" label="金额"></el-table-column>
	<el-table-column label="操作" align="center">
              <el-table-column label="添加" align="center">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">添加
                  </el-button>
                </template>
              </el-table-column> </el-table-column> 
      </el-table> </el-main></el-container>
    <div style="text-align: left">
      <el-dialog
        title="添加工资账套"
        :visible.sync="dialogVisible"
        width="50%"
        @close="emptySalary"
        :close-on-click-modal="false">
        <div style="display: flex;justify-content: flex-start;">
          <el-steps :active="index" direction="vertical">

            <el-step title="事由"></el-step>
            <el-step title="金额"></el-step>
	    <el-step title="使用类型"></el-step>
          </el-steps>
<div style="margin-left: 30px;display: flex;justify-content: center;align-items: center;width: 80%;">
            <div v-show="index==0">
              事由：
              <el-input
                placeholder="请输入事由..."
                @keyup.enter.native="next"
                size="mini"
                style="width: 200px"
                type="text"
                v-model="month.month_type">
              </el-input>
            </div>
		<div v-show="index==1">
		  事由类型：
		  <el-radio-group v-model="month.month_typeId">
                    <el-radio label="0">奖</el-radio>
                    <el-radio style="margin-left: 15px" label="1">罚</el-radio>
                  </el-radio-group>
		</div>
            <div v-show="index==2">
              金额：
              <el-input
                placeholder="请输入金额..."
                @keyup.enter.native="next"
                size="mini"
                style="width: 200px"
                type="number"
                v-model="month.month_salary">
              </el-input></div></div>
        </div>
        <div style="display: flex;align-items: center;justify-content: center;padding: 0px;margin: 0px;">
          <el-button round size="mini" v-if="index!=0" @click="index--">上一步</el-button>
          <el-button type="primary" round size="mini" @click="next" v-text="index==2?'完成':'下一步'"></el-button>
  </div></el-dialog></div>
</div>  
  </div>
</template>
<script>
  export default{
    data(){
      return {
	tableLoading: false,
	dialogVisible: false,
	index: 0,
        enames: '',
	emps: [],
	month: {
	 id: '',
	 eid: '',
	 month_type: '',
	 month_typeId: '',
	 month_salary: ''
	}
      }
    },
    methods: { 
	handleEdit(index, row) {
        this.dialogVisible = true;
	this.month.eid=row.id;
      }, 
    loadEmps(){  
        var _this = this;
        this.getRequest("/salary/month/month?enames="+this.enames).then(resp=> {
	  
          if (resp && resp.status == 200) {
	      
              _this.emps = resp.data;
          }
        })	
    },
      searchEmp() {
        this.loadEmps();
      },emptySalary(){
	 this.month={
          id: '',                                                                                                                 eid: '',
          month_type: '',
          month_typeId: '',
          month_salary: ''
         };
	  this.index=0;
	},next(){
        var _this = this;
        if (this.index == 2) {
	  alert(this.month.month_type+"\t"+this.month.month_salary+"\t"+this.month.month_typeId);
          if(this.month.month_type&&this.month.month_salary&&this.month.month_typeId){
            alert(123);
	    this.postRequest("/salary/month/month", this.month).then(resp=> {
                alert(JSON.stringify(resp.data));
		if (resp && resp.status == 200) {
                  var data = resp.data;

                  _this.dialogVisible = false;
                  _this.index = 0;
                  _this.loadEmps();
                }
              });
	  
         }}else{this.index++;}}
   }, mounted:function () {
      this.loadEmps();
    }
  }
</script>



