<template>
  <div>
    <el-container>
      <el-header style="text-align: left;padding-left: 0px;margin-top: 10px">
        <el-input
            placeholder="通过员工名搜索员工,记得回车哦..."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="enames">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>     
      </el-header>
      <el-main>
        <el-table
      :data="emps"
      size="mini"
      border
      stripe
      v-loading="tableLoading"
      style="width: 100%">
      <el-table-column
        type="selection"
        align="left"
        width="55">
      </el-table-column>
      <el-table-column
        prop="ename"
        align="left"
        fixed
        label="姓名"
        width="120">
      </el-table-column>
      <el-table-column
        prop="workID"
        width="120"
        align="left"
        label="工号">
      </el-table-column>
      <el-table-column                                                                                              
      </el-table-column>                                                                                            
      <el-table-column
        prop="salary.basicSalary"
        width="120"
        label="基本工资">
      </el-table-column>
      <el-table-column prop="salary.bonus" width="60" label="奖金"></el-table-column>
      <el-table-column prop="salary.lunchSalary" width="60" label="午餐补助"></el-table-column>
      <el-table-column prop="salary.trafficSalary" width="60" label="交通补助"></el-table-column>
      <el-table-column prop="salary.pensionBase" width="60" label="养老金基数"></el-table-column>
      
      <el-table-column prop="salary.medicalBase" width="60" label="医疗基数"></el-table-column>                                
      <el-table-column prop="salary.accumulationFundBase" width="60" label="公积金基数"></el-table-column>
      
      <el-table-column prop="month.monthTname" width="60" label="事件类型"></el-table-column>
      
      </el-table> </el-main></el-container>
                
  </div>
</template>
<script>
  export default{
    data(){
      return {
	tableLoading: false,
	dialogVisible: false,
	index: 0,
        enames: '',
	emps: []
      }
    },
    methods: {
     
      handleEdit(index, row) {
        this.dialogVisible = true;
        this.salary = row;
      },
    loadEmps(){
        
        var _this = this;
        this.getRequest("/salary/search/search?enames="+this.enames+"&depId=").then(resp=> {
	  
          if (resp && resp.status == 200) {
	      
              _this.emps = resp.data;
          }
        })	
    },
      searchEmp() {
        this.loadEmps();
      },
   }, mounted:function () {
      this.loadEmps();
    }
  }
</script>



