<template>
  <div>
    <el-container>
      <el-header style="text-align: left;padding-left: 0px;margin-top: 10px">
        <el-tag size="medium">请选择部门:</el-tag>
        <el-select size="mini" @change="loadEmps" v-model="depId" placeholder="请选择">
          <el-option
            v-for="item in deps"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-header>
      <el-main>
        <el-table
      :data="emps"
      size="mini"
      border
      stripe
      v-loading="tableLoading"
      style="width: 100%">
      <el-table-column
        type="selection"
        align="left"
        width="55">
      </el-table-column>
      <el-table-column
        prop="ename"
        align="left"
        fixed
        label="姓名"
        width="120">
      </el-table-column>
      <el-table-column
        prop="workID"
        width="120"
        align="left"
        label="工号">
      </el-table-column>
      <el-table-column                                                                                              
      </el-table-column>                                                                                            
      <el-table-column
        prop="salary.basicSalary"
        width="120"
        label="基本工资">
      </el-table-column>
      <el-table-column prop="salary.bonus" width="60" label="奖金"></el-table-column>
      <el-table-column prop="salary.lunchSalary" width="60" label="午餐补助"></el-table-column>
      <el-table-column prop="salary.trafficSalary" width="60" label="交通补助"></el-table-column>
      <el-table-column prop="salary.pensionBase" width="60" label="养老金基数"></el-table-column>
      <el-table-column prop="salary.pensionPer" width="60" label="养老金比率"></el-table-column>
      <el-table-column prop="salary.medicalBase" width="60" label="医疗基数"></el-table-column>                               <el-table-column prop="salary.medicalPer" width="60" label="医疗保险比率"></el-table-column> 
      <el-table-column prop="salary.accumulationFundBase" width="60" label="公积金基数"></el-table-column>
      <el-table-column prop="salary.accumulationFundPer" width="60" label="公积金比率"></el-table-column>
      	<el-table-column width="60":data="months" stripe label="事件类型">
		<el-table-column prop="month_type" width="60">
		</el-table-column>
	</el-table-column>
      <el-table-column prop="month.month_salary" width="60" label="事件金额"></el-table-column>
            <el-table-column label="操作" align="center">
              <el-table-column label="编辑" align="center">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑
                  </el-button>
                </template>
              </el-table-column> </el-table-column>
      </el-table> </el-main></el-container>
    <div style="text-align: left">
      <el-dialog
        title="添加工资账套"
        :visible.sync="dialogVisible"
        width="50%"
        @close="emptySalary"
        :close-on-click-modal="false">
        <div style="display: flex;justify-content: flex-start;">
          <el-steps :active="index" direction="vertical"> 
           
            <el-step title="交通补助"></el-step>
            <el-step title="午餐补助"></el-step>
           
            <el-step title="养老金"></el-step>
            <el-step title="医疗保险"></el-step>
            <el-step title="公积金"></el-step>
           
          </el-steps>
<div style="margin-left: 30px;display: flex;justify-content: center;align-items: center;width: 80%;">
            <div v-show="index==0">
              交通补助：
              <el-input
                placeholder="请输入交通补助..."
                @keyup.enter.native="next"
                size="mini"
                style="width: 200px"
                type="number"
                v-model="salary.trafficSalary">
              </el-input>
            </div>
            <div v-show="index==1">
              午餐补助：
              <el-input
                placeholder="请输入午餐补助..."
                @keyup.enter.native="next"
                size="mini"
                style="width: 200px"
                type="number"
                v-model="salary.lunchSalary">
              </el-input>
            </div>
            
            <div v-show="index==2">
              <div>
                养老金比率：
                <el-input
                  placeholder="请输入养老金比率..."
                  size="mini"
                  style="width: 200px"
                  type="number"
                  v-model="salary.pensionPer">
                </el-input>
              </div>
              <div style="margin-top: 10px">
                养老金基数：
                <el-input
                  placeholder="请输入养老金基数..."
                  size="mini"
                  @keyup.enter.native="next"
                  style="width: 200px"
                  type="number"
                  v-model="salary.pensionBase">
                </el-input>
              </div>
            </div>
            <div v-show="index==3">
              <div>
                医疗保险比率：
                <el-input
                  placeholder="请输入医疗保险比率..."
                  size="mini"
                  style="width: 200px"
                  type="number"
                  v-model="salary.medicalPer">
                </el-input>
              </div>
              <div style="margin-top: 10px">
                医疗保险基数：
                <el-input
                  placeholder="请输入医疗保险基数..."
                  size="mini"
                  @keyup.enter.native="next"
                  style="width: 200px"
                  type="number"
                  v-model="salary.medicalBase">
                </el-input>
              </div>
            </div>
            <div v-show="index==4">
              <div>
                公积金比率：
                <el-input
                  placeholder="请输入公积金比率..."
                  size="mini"
                  style="width: 200px"
                  type="number"
                  v-model="salary.accumulationFundPer">
                </el-input>
              </div>
              <div style="margin-top: 10px">
                公积金基数：
                <el-input
                  placeholder="请输入公积金基数..."
                  @keyup.enter.native="next"
                  size="mini"
                  style="width: 200px"
                  type="number"
                  v-model="salary.accumulationFundBase">
                </el-input>
              </div>
            </div></div>
        </div>
        <div style="display: flex;align-items: center;justify-content: center;padding: 0px;margin: 0px;">
          <el-button round size="mini" v-if="index!=0" @click="index--">上一步</el-button>
          <el-button type="primary" round size="mini" @click="next" v-text="index==4?'完成':'下一步'"></el-button>
  </div></el-dialog></div>
</div>
</template>
<script>
  export default{
    data(){
      return {
	tableLoading: false,
	dialogVisible: false,
	index: 0,
        depId: 1,
        deps: [],
	months: [],
	emps: [],
        salary: {
          id: '',
          basicSalary: '',
          trafficSalary: '',
          lunchSalary: '',
          bonus: '',
          pensionBase: '',
          pensionPer: '',
          medicalBase: '',
          medicalPer: '',
          accumulationFundBase: '',
          accumulationFundPer: ''
        }
      }
    },
    methods: {
      loadDeps(){
        var _this = this;
        this.getRequest("/salary/table/deps").then(resp=> {
          if (resp && resp.status == 200) {
            _this.deps = resp.data;
          }
        })
      },
      handleEdit(index, row) {
        this.dialogVisible = true;
        delete row.allSalary;alert(123+"\t"+JSON.stringify(row.salary));
        this.salary = row.salary;alert(this.salary.trafficSalary);
      },
      next(){
        var _this = this;
        if (this.index == 4) {
          if(this.salary.trafficSalary&&this.salary.lunchSalary&&this.salary.pensionBase&&this.salary.pensionPer&&this.salary.medicalBase&&this.salary.medicalPer&&this.salary.accumulationFundBase&&this.salary.accumulationFundPer){
          if (this.salary.id) {//更新
            _this.tableLoading = true;
            this.putRequest("/salary/table/search", this.salary).then(resp=> {
              _this.tableLoading = false;
              if (resp && resp.status == 200) {
                var data = resp.data;
             
                _this.dialogVisible = false;
                _this.index = 0;
                _this.loadEmps();
              }
            });
          }
         }}else{this.index++;}},
    loadEmps(){
        var _this = this;
        this.getRequest("/salary/search/search?enames=&depId="+this.depId).then(resp=> {
	
          if (resp && resp.status == 200) {
	      alert(JSON.stringify(resp.data));
              _this.emps = resp.data;
	      _this.months=resp.data.months;alert(JSON.stringify(_this.emps));
          }
        })	
    },
      emptySalary(){
        this.salary = {
          id: '',
          basicSalary: '',
          trafficSalary: '',
          lunchSalary: '',
          bonus: '',
          pensionBase: '',
          pensionPer: '',
          medicalBase: '',
          medicalPer: '',
          accumulationFundBase: '',
          accumulationFundPer: ''
        };
        this.index = 0;
      }
   }, mounted:function () {
      this.loadDeps();
      this.loadEmps();
    }
  }
</script>



